import React, { Component } from "react";
import makeAnimated from "react-select/animated";
import "./customSelect.scss";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import Select, { components } from "react-select";


const Option = (props) => {
  const clearValue = () => {
    props.clearValue();
  };

  return (
    <div>
      {props.label === "Clear" &&
      props.selectProps.value !== null &&
      props.selectProps.value.length > 0 ? (
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            borderTop: "1px solid gray",
            padding: "10px",
            textAlign: "center",
          }}
        >
          <div
            onClick={clearValue}
            style={{
              cursor: "pointer",
              fontFamily: "Poppins,sans-serif",
              color: "#21745d",
              fontSize: "11px",
            }}
          >
            Clear Selection
          </div>
          <div style={{ fontSize: "11px" }}>
            {props.selectProps.value !== null && props.selectProps.value.length}{" "}
            of {props.options.length - 1}
          </div>
        </div>
      ) : (
        props.label !== "Clear" && (
          <components.Option
            {...props}
            style={{ display: "flex", alignItems: "center" }}
          >
            <input
              type="checkbox"
              checked={props.isSelected}
              style={{ marginRight: "5px" }}
              className="purple_checkbox"
              onChange={() => null}
            />
            <div style={{ fontFamily: "Poppins,sans-serif", fontSize: "11px" }}>
              {props.label}
            </div>
          </components.Option>
        )
      )}
    </div>
  );
};

const CaretDownIcon = () => {
  return (
    <ArrowDropDownIcon
    />
  );
};
const DropdownIndicator = (props) => {
  return (
    <components.DropdownIndicator {...props}>
      <CaretDownIcon />
    </components.DropdownIndicator>
  );

}

const MultiValue = ({index, getValue,children,...props}) => {
  var length = getValue().length;
  return !index && <div style={{display:'flex',alignItems:'center'}}>
    <components.MultiValue {...props}> {props.data.label} </components.MultiValue>
    {
      length-1 ? <div style={{marginLeft:'5px'}}>+{length-1} more</div> : null
    }
    </div>

}
