const customStyles = {
  control: (base) => ({
    ...base,
    height: 34,
    minHeight: 34,
    maxHeight: 34,
    padding: 0,
    borderRadius: 4,
    fontSize: 11,
    color: "#000",
    background: "#F8F8F8 ",
  }),
  option: (provided, state) => ({
    ...provided,
    height: 28,
    display: "flex",
    alignItems: "center",
    backgroundColor: state.isSelected ? "#21745d" : "#fffff",
    "&:hover": {
      backgroundColor: "rgb(33,116,93,.5)",
    },
  }),
};

const animatedComponents = makeAnimated();
class CustomSelect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      optionSelected: [],
    };
  }

  handleChange = (selected) => {
    this.setState({
      optionSelected: selected,
    });
  };
  clearAllSelected = () => {
    this.setState({
      optionSelected: [],
    });
  };

  render() {
    const data = [
      ...this.props.options,
      ...[{ value: "clear", label: "Clear" }],
    ];

    return (
      <Select
        className="customSelect"
        options={data}
        isMulti
        isDisabled={this.props.disabled}
        styles={customStyles}
        closeMenuOnSelect={false}
        hideSelectedOptions={false}
        components={{ Option, MultiValue, animatedComponents, DropdownIndicator }}
        onChange={this.props.handleChange}
        allowSelectAll={true}
        value={this.props.selected}
        clearValue={this.props.clearAllSelected}
        noOptionsMessage="No records"
        placeholder={
          <div style={{ fontFamily: "Poppins,sans-serif", fontSize: "11px" }}>
            {this.props.placeholder}
          </div>
        }
      />
    );
  }
}
export default CustomSelect;
