import React from "react";
import './invites.scss';
import Button from "../../components/Controls/Button/Button";
import CloseIcon from '@mui/icons-material/Close';
import SendIcon from '@mui/icons-material/Send';
import WhatsappIcon from "../../assets/images/schedule/WhatsappIcon.svg";



export default function MessagePopup(props) {
  let count=0
  let currentPhone=''
 count = props.currentPhone.split(",").length - 1;
if(count){
currentPhone=props.currentPhone.substring(0,props.currentPhone.indexOf(','))+' +'+count
}else{
currentPhone=props.currentPhone
}

    return <div className="whatsapp-invites">
    <div className="whatsapp-invites-upper-div" style={{background:props.background,color:props.color}}>
      <div className="icon-heading-div">
        {props.sms ? null : <div className="whatsapp-icon"><img src={WhatsappIcon} /></div>}
        {
            props.sms ? <div className="whatsapp-number">Send Sms To  {currentPhone} </div> : <div className="whatsapp-number">Message To {currentPhone}</div>
        }
      </div>
      <div className="close-icon-div">
       <CloseIcon style={{color:props.sms ? '#333' : '#fff',cursor:'pointer'}} onClick={props.closePopUpHandler} />
      </div>

    </div>
    <div className="whatsapp-invites-middle-div">
    <textarea
          rows="2"
          className="whatsapp-text-area"
          cols="54"
          placeholder={
            props.currentMessage
              ? props.currentMessage
              : "Send Message"
          }
          onChange={(e) => {
           props.currentMessageHandler(e)
          }}
          value={
            props.currentMessage ? props.currentMessage : ""
          }
        ></textarea>
 </div>
 <div className="whatsapp-invites-Button-div">
 <Button
          backgroundColor="#04724D"
          color="#ffffff"
          width="160px"
          height="34px"
          fontWeight="bold"
          className="sendButtonWhatsapp"
          fontSize="11px"
          disabled={!(props.currentMessage && props.currentPhone )}
          onClick={props.sendWhatsapp}
        >
          Send 
          <SendIcon style={{  fontSize: "medium",color:'#fff',marginLeft:'20px' }} />
        </Button>
</div>

  </div>

}