import React from "react";
import "./invites.scss";
import Button from "../../components/Controls/Button/Button";
import CloseIcon from "@mui/icons-material/Close";
import SendIcon from "@mui/icons-material/Send";
import Autocomplete from "../../components/Atoms/AutoComplete";
import CustomizedInputs from "../../components/Atoms/CustomizedInputs/CustomizedInputs";

export default function EmailPopup(props) {
  return (
    <div className="email-invites">
      <div className="email-invites-upper-div">
        <div className="icon-heading-div">
          <div className="email-number"> Send Message</div>
        </div>
        <div className="close-icon-div">
          <CloseIcon
            style={{ color: "#333", cursor: "pointer" }}
            onClick={props.popupCloseHandler}
          />
        </div>
      </div>
      <div className="email-invites-middle-div">
        <div className="email-autocomplete">
          <Autocomplete
            placeholder=""
            multiple
            disabled={true}
            value={props.currentEmail}
            onChange={props.onChangeCurrentEmail}
            className="multiSelectContainer questionSkills"
            renderTags={(value, getTagProps) =>
              props.renderValues(value, getTagProps)
            }
            options={[]}
          />
        </div>
        <div className="email-input">
          <CustomizedInputs
            className={"email-subject-input"}
            placeholder="subject"
            value={props.emailSubject}
            onChange={(e) => {
              props.subjectChangeHandler(e);
            }}
          />
        </div>

        <div className="email-textarea">
          <textarea
            rows="2"
            className="email-text-area"
            cols="54"
            placeholder={
              props.currentMessage ? props.currentMessage : "Enter Message"
            }
            onChange={(e) => {
              props.onChangeMessage(e);
            }}
            value={props.currentMessage ? props.currentMessage : ""}
          ></textarea>
        </div>
      </div>
      <div className="email-invites-Button-div">
        <Button
          backgroundColor="#04724D"
          color="#ffffff"
          width="160px"
          height="34px"
          fontWeight="bold"
          className="sendButtonWhatsapp"
          fontSize="11px"
          disabled={
            !(props.currentMessage && props.emailSubject && props.currentEmail)
          }
          onClick={props.sendEmail}
        >
          Send
          <SendIcon
            style={{ fontSize: "medium", color: "#fff", marginLeft: "20px" }}
          />
        </Button>
      </div>
    </div>
  );
}
